.bg{
    background-color: white;
}
.bi.bi-arrow-right{
transition: 0.4s;
cursor: pointer;
}
.bi.bi-arrow-right:hover{
margin-left: 5px;
margin-right: -5px;
transition: 0.4s;
cursor: pointer;
color: rgb(203, 0, 159);
}
.link{
color: rgb(46, 90, 252);
}
.input-group-text{
    border-radius: 1.5rem;
    margin-top: 0.5rem; 
    background-color: #eff2f5;
    border-color: #eff2f5;
}
.form-control.form-control-lg{
    width: 20rem;
    border-radius: 1.5rem;
    margin-top: 0.5rem;
    font-size: 0.9rem;
    background-color: #eff2f5;
    border-color: #eff2f5;    
}

.no-focus-outline:focus {
    outline: none;
    box-shadow: none;
  }
  .container{
    transition: width 0.3s ease;
  }
  .image-container {
    position: relative;
    width: 100%;
    height: 55vh;
    transition: 0.3s;
  }
  
  .poster {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    object-fit: cover;
    object-position: bottom;
    height: 100%;
  }
  
  .text-overlay {
    position: absolute;
    top: 35%;
    padding-left: 3rem;
    width: 100%;
    color: #ffffff;
    transition: 0.3s;
  }
  
  
  @media (min-width: 426px) 
  {
    .poster {
      object-position: center;
      transition: 0.3s;
      height: 55vh;
    }
    
  }

#Navbar2{
  margin-top: 1rem;
.navbar-nav::after {
    content: "";
    position: absolute;
    top: 48.5px;
    bottom: 12px;
    margin-top: 10px;
    width: 96.5%;
    height: 1.5px;
    background-color: #b5b5b5;
  }
}

.navbar-toggler{
  font-size: 0.8rem;
}
.nav-item{
  font-size: 0.9rem;
}


.navbar-brand{
  font-size: 1rem;
}
.horizontal1{
  background-color: #05182c;
}
.nav2toggle{
  color: #05182c;
}
.bi.bi-caret-down-fill{
  font-size: 13px;
}

.nav-link {
  height: 3.25rem;
  
}
.nav-link.active {
  position: relative;
  overflow: hidden;
  font-weight: 500;
}

.nav-link.active::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 90%;
  height: 1.5px;
  background-color: #000;
  z-index: 1;
}

.btn.btn-secondary{
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 0.85rem;
  color: #000;
  font-weight: 500;
  background-color: #edeef0;
  border: none;
  
}
.btn.btn-primary{
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-size: 0.85rem;
  font-weight: 500;
  border: none;
}

.rounded-circle.avatar{
  width: 40px;
  height: 40px;
}
.cardimg{
  position: relative;
    width: 100%;
    object-fit: cover;
    object-position: bottom;
    height: 35vh;
}

.btn.btn-outline-danger.py-1{
  position: relative;
padding-left: 12.5rem;
padding-right: 12.5rem;
}
.WriteNewPostLog{
  position: fixed;
  top:80%;
  left: 80%;
}
@media (max-width: 991.5px) {
  /* Styles for mobile */
  .nav-link.active::after {
    display: none;
  }
  .navbar-nav::after {
    display: none;
  }
  .posterfont {
    font-size: 1.6rem;
  }  
  .iconswithviews{
    display: none;
  }
  .shareicon{
    display: none;
  }
  .col2 {
    display: none;
  }
  .col1 {
    width: 100%;
  }
}
@media (min-width: 426px) {
  #Navbar2{
  .navbar-brand {
    display: none;
  }}
}
@media (min-width: 991.5px){
  
  .text-muted.views{
    display: none;
  }
  .sharebutton{
    display: none;
  }
  .WriteNewPostLog{
    display: none;
  }
}

/* pop up css */
@media (max-width: 768px) {
  .modal {
    bottom: 0;
    right: 0;
    left: 0;
    transform: translateY(100%);
    transition: transform 0.3s ease-in-out;
  }
  .modal.show {
    transform: translateY(0);
  }
  .fade.modal.show{
    padding: 0;
  }
 
}
.btn-close{
  display: none;
}

